<template>
  <div class="todo-container">
    <h2>ÂæÖÂäû‰∫ãÈ°π</h2>
        <input
      v-model="searchQuery"
      placeholder="üîç ÊêúÁ¥¢‰ªªÂä°..."
      class="search-box"
    />
    <div class="input-container">
      <input v-model="newTask" placeholder="ËæìÂÖ•‰ªªÂä°..." @keyup.enter="addTask" />
      <select v-model="priority">
        <option value="high">üî• È´ò</option>
        <option value="medium">‚ö° ‰∏≠</option>
        <option value="low">‚úÖ ‰Ωé</option>
      </select>
      <input type="date" v-model="dueDate" />
      <button @click="addTask">Ê∑ªÂä†</button>
    </div>

    <!-- Ê∏ÖÁ©∫ÊåâÈíÆ -->
    <button class="clear-button" @click="clearCompletedTasks" v-if="tasks.some(task => task.completed)">
      Ê∏ÖÁ©∫Â∑≤ÂÆåÊàê‰ªªÂä°
    </button>
    <!-- ÂØºÂá∫ÂíåÂØºÂÖ•‰ªªÂä°ÊåâÈíÆ -->
    <button class="export-button" @click="exportTasks">ÂØºÂá∫‰ªªÂä°Êï∞ÊçÆ</button>
    <input type="file" @change="importTasks" class="import-input" />
    <ul>
<transition-group name="list" tag="ul">
<li v-for="(task, index) in filteredTasks" :key="index" :class="task.priority">
        <span :class="{ completed: task.completed }" @click="toggleTask(index)">
          {{ task.text }} ({{ getPriorityText(task.priority) }})
          <small v-if="task.dueDate">üóì Êà™Ê≠¢Êó•Êúü: {{ task.dueDate }}</small>
          <small v-if="task.completedTime">‚úÖ ÂÆåÊàêÊó∂Èó¥: {{ task.completedTime }}</small>
        </span>
        <button v-if="editingIndex !== index" @click="editTask(index)">ÁºñËæë</button>
        <button v-if="editingIndex === index" @click="saveEdit(index)">‰øùÂ≠ò</button>
        <button @click="removeTask(index)">Âà†Èô§</button>
      </li>
     </transition-group>
    </ul>
  </div>
</template>


<script>
export default {
  data() {
    return {
      newTask: '',
      priority: 'medium',
      dueDate: '',
      searchQuery: '',  // ÊêúÁ¥¢Ê°ÜËæìÂÖ•ÂÜÖÂÆπ
      tasks: JSON.parse(localStorage.getItem('tasks')) || [],
      editingIndex: null,
      editText: ''
    };
  },
  computed: {
    sortedTasks() {
      return [...this.tasks].sort((a, b) => {
        const priorityOrder = { high: 3, medium: 2, low: 1 };
        if (priorityOrder[a.priority] !== priorityOrder[b.priority]) {
          return priorityOrder[b.priority] - priorityOrder[a.priority];
        }
        if (a.dueDate && b.dueDate) {
          return new Date(a.dueDate) - new Date(b.dueDate);
        }
        return 0;
      });
    },
    filteredTasks() {
      return this.sortedTasks.filter(task =>
        task.text.toLowerCase().includes(this.searchQuery.toLowerCase()) // ‰ªªÂä°ÂêçÂåπÈÖçÊêúÁ¥¢ÂÖ≥ÈîÆËØç
      );
    }
  },
  methods: {
      showToast(message) {
    alert(message); // ÁÆÄÂçïÊñπÂºèÔºöÁî® alert ÊèêÁ§∫
  },
    addTask() {
      if (this.newTask.trim()) {
        this.tasks.push({
          text: this.newTask,
          completed: false,
          priority: this.priority,
          dueDate: this.dueDate,
          completedTime: null
        });
        this.newTask = '';
        this.priority = 'medium';
        this.dueDate = '';
        this.saveTasks();
        this.showToast("‚úÖ ‰ªªÂä°Â∑≤Ê∑ªÂä†ÔºÅ");
      }
    },
    toggleTask(index) {
      this.tasks[index].completed = !this.tasks[index].completed;
      if (this.tasks[index].completed) {
        this.tasks[index].completedTime = new Date().toLocaleString();
      } else {
        this.tasks[index].completedTime = null;
      }
      this.saveTasks();
    },
    removeTask(index) {
      this.tasks.splice(index, 1);
      this.saveTasks();
      this.showToast("‚ùå ‰ªªÂä°Â∑≤Âà†Èô§ÔºÅ");
    },
    clearCompletedTasks() {
      this.tasks = this.tasks.filter(task => !task.completed);
      this.saveTasks();
      this.showToast("‚úÖ Â∑≤Ê∏ÖÁ©∫ÊâÄÊúâÂ∑≤ÂÆåÊàê‰ªªÂä°ÔºÅ");
    },
    editTask(index) {
      this.editingIndex = index;
      this.editText = this.tasks[index].text;
    },
    saveEdit(index) {
      if (this.editText.trim()) {
        this.tasks[index].text = this.editText;
        this.editingIndex = null;
        this.saveTasks();
      }
    },
    saveTasks() {
      localStorage.setItem('tasks', JSON.stringify(this.tasks));
    },
    exportTasks() {
      const dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(this.tasks, null, 2));
      const downloadAnchor = document.createElement("a");
      downloadAnchor.setAttribute("href", dataStr);
      downloadAnchor.setAttribute("download", "tasks.json");
      document.body.appendChild(downloadAnchor);
      downloadAnchor.click();
      document.body.removeChild(downloadAnchor);
      this.showToast("‚úÖ Â§á‰ªΩÊàêÂäüÔºÅ");
    },
    importTasks(event) {
      const file = event.target.files[0];
      if (!file) return;
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const importedTasks = JSON.parse(e.target.result);
          if (Array.isArray(importedTasks)) {
            this.tasks = importedTasks;
            this.saveTasks();
            alert("‰ªªÂä°Êï∞ÊçÆÂØºÂÖ•ÊàêÂäüÔºÅ");
          } else {
            this.showToast("‚ö†Ô∏è Êñá‰ª∂Ê†ºÂºèÈîôËØØÔºåËØ∑ÈÄâÊã©Ê≠£Á°ÆÁöÑ JSON Êñá‰ª∂ÔºÅ");
          }
        } catch (error) {
          this.showToast("‚ùå ÂØºÂÖ•Â§±Ë¥•ÔºåËØ∑Á°Æ‰øù JSON Ê†ºÂºèÊ≠£Á°ÆÔºÅ");
        }
      };
      reader.readAsText(file);
    },
    getPriorityText(priority) {
      if (priority === 'high') return 'üî• È´ò';
      if (priority === 'medium') return '‚ö° ‰∏≠';
      if (priority === 'low') return '‚úÖ ‰Ωé';
      return '';
    }
  }
};
</script>




